<template>
  <div class="main">
    <nav class="c-breadcrumb-bg" aria-label="breadcrumb">
      <div class="container">
        <ol class="breadcrumb">
          <li class="breadcrumb-item"><router-link :to="{ name: 'Shop'}" >首頁</router-link></li>
          <li class="breadcrumb-item active" aria-current="page">購物車</li>
        </ol>
      </div>
    </nav>
    <div class="container mt-5" v-if ="isCartEmpty">
      <div class="text-center mt-2">
        <font-awesome-icon :icon="['fas', 'shopping-cart']" size="6x" class="text-secondary mb-3" />
        <h3 class="h5 font-weight-bold">你的購物車是空的</h3>
        <p>記得加入商品到你的購物車</p>
        <router-link class="btn btn-info d-inline-block" to="/">繼續購物</router-link>
      </div>
    </div>
    <div class="container" v-else>
      <div class="row my-5">
        <div class="col-md-7">
          <h2 class="h3">購物車</h2>
          <table class="table mt-3">
            <tbody>
              <template v-if="cartData.carts">
                <tr v-for="item in cartData.carts" :key="item.id">
                  <td class="align-middle text-center" >
                    <div :style="{backgroundImage: `url(${item.product.imageUrl})`}" class="c-card-img"></div>
                  </td>
                  <td class="align-middle">
                    {{ item.product.title }}
                    <div class="text-success" v-if="item.coupon">
                      已套用優惠券
                    </div>
                  </td>
                  <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
                  <td class="align-middle text-right">
                    <span :class="{ 'line-through': item.coupon }">
                      {{ item.total | currency }}
                    </span>
                    <div class="c-price mt-0" v-if="item.coupon">
                      {{ item.final_total | currency }}
                    </div>
                  </td>
                  <td class="align-middle">
                    <font-awesome-icon :icon="['fas', 'trash-alt']" @click.prevent="deleteCart(item.id)"
                    class="text-danger c-pointer" />
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
        <div class="col-md-5">
          <h2 class="h3 mb-3">訂單小計</h2>
          <div class="p-3 border">
            <div class="d-flex justify-content-between mb-2">
              <span>小計</span>
              <span>{{ cartData.total }}</span>
            </div>
            <div class="d-flex justify-content-between mb-3 discount">
              <span>折扣</span>
              <span>{{ discount }}</span>
            </div>
            <div class="d-flex justify-content-between">
              <span class="c-final-total">總計</span>
              <span>{{ cartData.final_total }}</span>
            </div>
            <div class="input-group">
              <input type="text" class="form-control" v-model="couponCode" placeholder="輸入優惠券代碼">
              <div class="input-group-append" @click="addCouponCode">
                <span class="input-group-text btn-warning c-pointer">套用優惠碼</span>
              </div>
            </div>
            <p class="small text-success mt-1">現在輸入 welcome 即可享有折扣價喔!</p>
            <router-link :to="{ name: 'Order' }" class="btn btn-warning w-100">前往結帳</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Cart',
  computed: {
    ...mapState(['cartData']),
    couponCode: {
      get() {
        return this.$store.state.couponCode;
      },
      set(value) {
        this.$store.commit('SET_COUPONCODE', value);
      },
    },
    discount() {
      const vm = this;
      return vm.cartData.total - vm.cartData.final_total;
    },
    isCartEmpty() {
      if (this.cartData.carts.length > 0) {
        return false;
      }
      return true;
    },
  },
  methods: {
    deleteCart(id) {
      this.$store.dispatch('removeCart', id);
    },
    addCouponCode() {
      this.$store.dispatch('addCouponCode');
    },
  },
};
</script>
